body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 60px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.gif-container{
  display: flex;
  flex-wrap: wrap;
}

.gif-item{
  padding: 10px;
  /* max-width: 200px; */
  height: 100%;
  overflow: hidden;
  border: 1px solid black;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
}
.gif-item img{
  height: 170px;
  width: 100%;
}
.gif-item p{
  max-width: 100%;
  text-align: center;
}